<template>
  <div id="settings-container">
    <v-row>
      <theme-picker v-model="currentTheme"></theme-picker>
    </v-row>
    <v-row>
      <v-checkbox v-model="inputOutlines" v-on:change="inputOutlinesToggled" label="Add outline to input fields"></v-checkbox>
    </v-row>
    <v-row>
      <colour-picker 
          :initial="initialText" 
          :on-update="updateTextColour" 
          :theme="currentTheme" 
          :themeProperty="'text'"
          :title="'Change Text Colour'">
      </colour-picker>
      <colour-picker 
          :initial="initialCard" 
          :on-update="updateCardColour" 
          :theme="currentTheme" 
          :themeProperty="'card'"
          :title="'Change Card Colour'">
      </colour-picker>
    </v-row>
  </div>
</template>

<script>
import ColourPicker from './ColourPicker';
import ThemePicker from './ThemePicker';

/**
 * Sets the colour of the property within the current theme.
 */
function _setColour(color, property) {
  this.$vuetify.theme.themes[this.currentTheme][property] = color;
}

export default {
  components: {
    'colour-picker': ColourPicker,
    'theme-picker': ThemePicker
  },
  data: function() {
    let theme = this.$vuetify.theme.isDark ? 'dark' : 'light';
    return {
      currentTheme: theme,
      initialCard: this.$vuetify.theme.themes[theme].card || '',
      initialText: this.$vuetify.theme.themes[theme].text || '',
      inputOutlines: this.$store.state.userSettings.inputFieldOutline
    }
  },
  methods: {
    inputOutlinesToggled: function() {
      if (this.inputOutlines) {
        this.$store.commit('enableInputOutline');
      } else {
        this.$store.commit('disableInputOutline');
      }
    },
    updateCardColour: function(color) {
      _setColour.call(this, color, 'card');
    },
    updateTextColour: function(color) {
      _setColour.call(this, color, 'text');
    },
  },
  name: 'Settings'
}
</script>

<style scoped lang="less">

#settings-container {
  padding: 15px;
}

</style>
