<template>
  <div id="colour-picker-container">
      <v-subheader>{{title}}</v-subheader>
      <chrome-picker v-model="color" @input="updateValue"></chrome-picker>
  </div>
</template>

<script>
import { Chrome } from 'vue-color';

  export default {
    components: {
        'chrome-picker': Chrome,
    },
    data: function() {
        return {
            color: this.initial
        }
    },
    methods: {
      updateValue: function() {
        this.onUpdate(this.color.hex);
      }
    },
    name: 'ColourPicker',
    props: {
        initial: String,
        onUpdate: Function,
        theme: String,
        themeProperty: String,
        title: String
    },
    watch: { 
      theme: function(theme) {
        let newColour = this.$vuetify.theme.themes[theme][this.themeProperty];
        if (newColour) {
          this.color = newColour;
        } else {
          this.color = '';
        }
      }
    }
  }
</script>

<style scoped>
#colour-picker-container {
  margin: 5px;
}
</style>
