import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';
import UserSettings from './plugins/UserSettings';
import { router } from './router';
import { store } from './store/store';

Vue.config.productionTip = false;

new Vue({
  vuetify,
  router,
  store,
  render: h => h(App),
  beforeMount: function() {
    // Load text and card colours from a user settings service
    let that = this;
    const promise = new Promise((resolve) => {
      UserSettings().then(function(data) {
        that.$vuetify.theme.themes.light.card = data.light.card;
        that.$vuetify.theme.themes.light.text = data.light.text;
        that.$vuetify.theme.themes.dark.card = data.dark.card;
        that.$vuetify.theme.themes.dark.text = data.dark.text;
        resolve();
      });
    });
    return promise;
  }
}).$mount('#app')
