import Vue from 'vue';
import VueRouter from 'vue-router';
import Form from './components/Form';
import Settings from './components/Settings';

Vue.use(VueRouter);

const routes = [
    { 
        path: '/form', 
        component: Form 
    },
    { 
        path: '/settings', 
        component: Settings
    },
    {
        path: '/',
        redirect: '/settings'
    }
];
export const router = new VueRouter({
    routes
});
  