import Vue from 'vue';
import Vuetify from 'vuetify/lib';

Vue.use(Vuetify);

/**
 * This is an important piece of config to support changing card and text colours.
 * The properties 'card' and 'text' will be added into the existing theme configs for light/dark
 * which vuetify provides as standard.
 * Enabling customProperties creates a css variable for each theme color (primary, secondary etc)
 * See https://vuetifyjs.com/en/customization/theme/#custom-properties
 * 
 */
const themeConfig = {
    theme: {
        options: {
            customProperties: true
        },
        themes: {
            light: {
                card: null,
                text: null
            },
            dark: {
                card: null,
                text: null
            }
        }
    }
};
export default new Vuetify(themeConfig);
