import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        userSettings: {
            inputFieldOutline: false
        }
    },
    mutations: {
        disableInputOutline: function(state) {
            state.userSettings.inputFieldOutline = false;
        },
        enableInputOutline: function(state) {
            state.userSettings.inputFieldOutline = true;
        }
    }
});