import axios from 'axios';
import Vue from 'vue';
import Vuetify from 'vuetify/lib';

Vue.use(Vuetify);

/**
 * Replicates fetching user settings from a service
 * In order to see this working, go to <project_root>/server and run 'node server.js'
 */
const userSettings = function() {
    let promise = axios.get('http://localhost:8000')
      .then(function(response) {
          return response.data;
      });
    return promise;
  };
  
  export default userSettings;